r\n    display: block;\r\n    background-position: center;\r\n    background-repeat: no-repeat;\r\n    background-size: contain;\r\n    transition: 0.1s;\r\n    &:hover {\r\n      transform: scale(1.1);\r\n    }\r\n  }\r\n  .github {\r\n    justify-self: end;\r\n    height: 2.5em;\r\n    aspect-ratio: 1;\r\n    border-radius: 50%;\r\n    background-image: url('../images/er-github.webp');\r\n  }\r\n  .rss-logo {\r\n    height: 1.75em;\r\n    aspect-ratio: 2.7/1;\r\n    background-image: url('../images/rs-school-js.svg');\r\n  }\r\n}\r\n\r\n// content\r\n\r\n.garage__total-counter,\r\n.winners__total-counter {\r\n  &::before {\r\n    content: ' (';\r\n  }\r\n  &::after {\r\n    content: ')';\r\n  }\r\n}\r\n\r\n.nav__button,\r\n.garage__race-control-button,\r\n.car-settings__submit,\r\n.pagination__button {\r\n  padding: 0.3em 0.5em;\r\n  margin: 0.2em;\r\n  border-radius: 0.4em;\r\n  color: #fff;\r\n  background-color: #696969;\r\n  box-shadow: inset 0 0 4px 2px #fff8, 0 0 4px 2px #00000080;\r\n  border: none;\r\n  outline: none;\r\n  text-transform: capitalize;\r\n  &:hover {\r\n    cursor: pointer;\r\n    box-shadow: inset 0 0 4px 2px #fff;\r\n  }\r\n  &:disabled {\r\n    pointer-events: none;\r\n    opacity: 0.3;\r\n  }\r\n}\r\n.pagination {\r\n  display: inline-block;\r\n  margin: 0.3em auto;\r\n  &__counter {\r\n    &::-webkit-inner-spin-button,\r\n    &::-webkit-outer-spin-button {\r\n      -webkit-appearance: none;\r\n    }\r\n    color: #fff;\r\n    background-color: transparent;\r\n    border: none;\r\n    pointer-events: none;\r\n    width: 2em;\r\n    text-align: center;\r\n  }\r\n}\r\n.garage__heading,\r\n.winners__heading {\r\n  display: inline-block;\r\n  vertical-align: middle;\r\n  text-transform: capitalize;\r\n  margin-left: 1em;\r\n}\r\n",".garage {\n  .race-track {\n    margin: 0.5em auto;\n  }\n  .car {\n    padding: 0.2em;\n    background-color: #fff3;\n  }\n  .car__name {\n    display: inline-block;\n    vertical-align: top;\n    padding: 0.5em;\n  }\n  .car__button {\n    height: 32px;\n    aspect-ratio: 1;\n    outline: none;\n    border: none;\n    background-color: transparent;\n    background-size: 80%;\n    background-position: center;\n    background-repeat: no-repeat;\n    transition: all 0.1s;\n    &:hover {\n      transform: scale(1.1);\n      cursor: pointer;\n    }\n    &:active {\n      transition: unset;\n      transform: scale(1);\n    }\n    &:disabled {\n      pointer-events: none;\n      opacity: 0.3;\n    }\n  }\n  .car__edit {\n    background-image: url('../images/car-edit.svg');\n  }\n  .car__start {\n    background-image: url('../images/car-start.svg');\n  }\n  .car__stop {\n    background-image: url('../images/car-stop.svg');\n  }\n  .car__delete {\n    background-image: url('../images/car-delete.svg');\n  }\n  .car__track {\n    margin: 0.5em auto;\n    width: 100%;\n    -webkit-appearance: none;\n    appearance: none;\n    outline: none;\n    background-color: transparent;\n    background-image: url('../images/finish-flag.svg');\n    background-size: contain;\n    background-position: right;\n    background-repeat: no-repeat;\n    border-bottom: 1px dashed #808080;\n\n    &::-webkit-slider-thumb {\n      box-shadow: inset 0 -1.5em 1em -1em #fff;\n      -webkit-appearance: none;\n      appearance: none;\n      height: 32px;\n      aspect-ratio: 2.6/1;\n      background-color: var(--car-color);\n      -webkit-mask-image: url('../images/tesla.svg');\n      -webkit-mask-size: contain;\n      -webkit-mask-repeat: no-repeat;\n    }\n  }\n}\n\n.car-settings {\n  &__name {\n    background-color: transparent;\n    border: 1px solid #696969;\n    color: #fff;\n    padding: 0.3em;\n  }\n  &__color {\n    margin-left: 0.3em;\n    vertical-align: middle;\n    background-color: transparent;\n    border: 1px solid #696969;\n    &:hover {\n      cursor: pointer;\n      border-color: var(--car-color);\n      box-shadow: 0 0 1px 1px var(--car-color);\n    }\n  }\n}\n\n.modal {\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  background-color: #0008;\n  &__dialog {\n    margin: 1.5em;\n    padding: 1.5em;\n    box-shadow: 0 0 0.2em 0.2em var(--car-color);\n    background-color: #000;\n  }\n\n  /*   position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0;\n */\n  //&.garage__car-update {\n\n  // display: flex;\n  // justify-content: center;\n  // align-items: center;\n  // background-color: #000c;\n  //}\n}\n",".winners {\n  &-table {\n    border-collapse: collapse;\n    &__th {\n      background-color: $color-best;\n    }\n    &__th,\n    &__td {\n      border: 1px solid $color-best;\n      padding: 0.4em;\n    }\n    &__car {\n      width: 4em;\n      aspect-ratio: 2.6/1;\n      background-color: var(--car-color);\n      -webkit-mask-image: url('../images/tesla.svg');\n      -webkit-mask-repeat: no-repeat;\n      -webkit-mask-position: center;\n      -webkit-mask-size: 80%;\n      mask-image: url('../images/tesla.svg');\n      mask-size: contain;\n      mask-repeat: no-repeat;\n      mask-position: center;\n    }\n  }\n}\n"],"sourceRoot":""}]);
